import React,{useEffect, useState} from 'react';
import { useHistory,useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let 박스 = styled.div`
    padding : 20px;
`;

let 제목 = styled.h4`
    font-size : 25px;
    color : ${props=>props.색상}
`;

function 배너(){
    return (
        <div className="my-alert-red">
            <p>재고가 얼마 안남았습니다.</p>
        </div>
    )
}


function Detail(props){

    let [배너값,배너변경] = useState(true);
    let [입력,입력변경] = useState('');

    useEffect(()=>{
        let 타이머 = setTimeout(()=>배너변경(false),2000);
        console.log('업데이트');
        // return은 컴포넌트가 끝날때 콜백함수 정의
        return ()=>{ clearTimeout(타이머)};
    },[배너값]);


    
    let { id } = useParams();
    let history = useHistory();
    let detail = props.shoes.find((x)=>x.id==id);
    
    return(
        <div className="container">
            <박스>
                <제목 className="red">Detail</제목>
            </박스>
            <div>{입력}</div>
            <input onChange={(e)=>{입력변경(e.target.value)}}/>
            {
                배너값 == true 
                ? 배너() :
                null
            }
           
            <div className="row">
                <div className="col-md-6">
                <img src={detail.image} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                <h4 className="pt-5">{detail.title}</h4>
                <p>{detail.content}</p>
                <p>{detail.price}원</p>
                <Info info={props.info[id]}/>
                <button className="btn btn-danger" onClick={ ()=>{props.setInfo([9,10,11])}}>주문하기</button> 
                &nbsp;
                <button className="btn btn-danger" onClick={()=>history.push('/')}>뒤로가기</button> 
                </div>
            </div>
          </div> 
    )
}


function Info(props){
    return (
        <p>재고 : {props.info}</p>
    )
}

export default Detail;